
import axios from 'axios'

const ajax = (options) => {
    let _react = options.react === undefined ? true : options.react
    return axios(options)
        .then(res => {
            if (res.statusText === "OK") {
                if (_react) console.log('')
            } else {
                if (_react) console.log('')
            }
            return res
        })
        .catch(err => {
            return err
            console.log('')
            
        })
    
}

export default ajax